<template>
	<div id="order-teble">
		<public-header title="店内桌台"></public-header>
		<div class="content">
			<div class="tabs-list border-1px-b">
				<div class="tabs-item" :class="{active: cid == 0}" @click="onToggleStatus(0)">全部</div>
				<div class="tabs-item" :class="{active: cid == value.id}" @click="onToggleStatus(value.id)" v-for="(value, index) in table_category" :key="value.id">{{value.title}}</div>
			</div>
			<van-pull-refresh v-model="isRefresh" @refresh="onPullDownRefresh()">
			<div class="table-group" v-if="tables.length > 0">
				<div class="table-list">
					<van-row gutter="10">
						<van-col span="8" v-for="(item, index) in tables" :key="item.id">
							<div class="table-item" :class="{'active': item.status == 1}" @click="onChangeStatus(item, index)">
								<div class="type">{{item.title}}</div>
								<div class="status" v-if="item.status == 1">空闲中</div>
								<div class="status" v-else>已开台</div>
							</div>
						</van-col>
					</van-row>
				</div>
			</div>
			<div class="no-data" v-else>
				<img src="static/img/order_no_con.png" alt="">
				<p>暂无队列!</p>
			</div>
			</van-pull-refresh>
		</div>
		<iloading v-if="showPreLoading"></iloading>
	</div>
</template>

<script>
	import publicHeader from "@/components/header"
	import {getData} from "@/controller/funcCommon"
	export default {
		data() {
			return {
				cid: 0,
				tables: {},
				table_category: {},
				isRefresh: false,
				showPreLoading: true
			}
		},
		components: {
			publicHeader,
		},
		methods: {
			onLoad(force){
				if(force){
					this.tables = {}
				}
				var that = this;
				getData({
					vue: this,
					url: 'manage/order/table/index',
					data: {
						cid: that.cid
					},
					success: (res) => {
						that.tables = [...that.tables, ...res.tables];
						that.table_category = res.table_category;
						that.isRefresh = false;
					}
				});
			},
			onToggleStatus(value){
				value = parseInt(value);
				if(this.cid == value) {
					return;
				}
				this.cid = value;
				this.onLoad(true);
			},
			onChangeStatus(item, index){
				var that = this;
				that.util.jspost({
					url: 'manage/order/table/status',
					data: {
						id: item.id,
						status: item.status
					},
					confirm: '确定修改桌台状态吗？',
					success:function(){
						if (item.status == 1){
							that.tables[index].status = 2;
						} else {
							that.tables[index].status = 1;
						}
					}
				});
			},
			onPullDownRefresh(){
				this.onLoad(true);
			}
		},
		mounted(){
			this.onLoad();
		},
	}
</script>

<style>

	#order-teble .content .tabs-list{
		width: 100%;
		height: 45px;
		overflow-x: scroll;
		white-space: nowrap;
		background: #fff;
		color: #333;
		position: fixed;
		top: 46px;
		z-index: 10;
		left: 0;
		right: 0;
	}
	#order-teble .content .van-pull-refresh{
		padding-top: 45px;
	}
	#order-teble .content .tabs-list::-webkit-scrollbar{
		display: none;
	}
	#order-teble .content .tabs-list .tabs-item{
		display: inline-block;
		height: 100%;
		width: 24%;
		margin: 0px;
		line-height: 45px;
		text-align: center;
		position: relative;
		font-size: 14px;
		color: #333;
	}
	#order-teble .content .tabs-list .tabs-item.active{
		color: #33aafc;
	}
	#order-teble .content .tabs-list .tabs-item.active:after{
		content: '';
		position: absolute;
		left: 0;bottom: 0;
		width: 100%;
		height: 0;
		border-bottom: .1rem solid #33aafc;
	}
	#order-teble .content .table-group{
		padding: 10px;
	}
	#order-teble .content .van-col{
		padding-bottom: 10px;
	}
	#order-teble .content .table-group .table-item{
		text-align: center;
		height:108px;
		background:#31AAFB;
		border-radius:5px;
	}
	#order-teble .content .table-group .table-item.active{
		background:#ff2b4d;
	}
	#order-teble .content .table-group .table-item .type{
		padding-top:26px;
		color:#fff;
		font-size:14px;
		opacity:.7;
	}
	#order-teble .content .table-group .table-item .status{
		padding-top:15px;
		color:#fff;
		font-size:16px;
		font-weight:bold;
	}
</style>


